import { inputRequiredValidateObj } from '@/common/validator/common';

// 
export let delcommafy = (num) => {
  if (!num) return num;
  num = num.toString();
  num = num.replace(/,/gi, '');
  if (num.indexOf('.00') > 0) num = parseInt(num);
  return num;
};
export let priceFormat = (num, n) => {
  n = n || 2;
  let symbol = ',';
  if (num === null) return null;
  if (typeof num !== 'number') throw new TypeError('The num parameter should be of the number type.');
  if (n < 0) throw new Error('Parameter n should not be less than 0.');
  let hasDot = parseInt(num) !== num; // numtrue
  let m = n !== undefined && n != null ? n : 1;
  num = m === 0 ? num.toFixed(m) + '.' : hasDot ? (n ? num.toFixed(n) : num) : num.toFixed(m);
  symbol = symbol || ',';
  num = num.toString().replace(/(\d)(?=(\d{3})+\.)/g, function (match, p1) {
    return p1 + symbol;
  });
  if (n === 0 || (!hasDot && !n)) {
    // n0num
    num = num.substring(0, num.indexOf('.'));
  }
  return num;
};

let checkSettlementCycle = (scope) => {
  return {
    trigger: ['blur', 'change'],
    required: true,
    validator: (rule, value, callback) => {
      let cycleValue = scope.revenueFormData.intervalValue;
      let timeUnit = scope.revenueFormData.intervalTypeCode;
      if (!cycleValue || !timeUnit) {
        return callback(new Error('Please enter content'));
      }
      if (cycleValue && timeUnit) {
        if (cycleValue >= 10000 || cycleValue <= 0) {
          return callback(new Error('Please enter a number between 1 and 9999.'));
        } else {
          return callback();
        }
      }
      return callback();
    },
  };
};

let checkStartDate = (scope) => {
  return {
    required: true,
    validator: (rule, value, callback) => {
      let startDate = scope.revenueFormData.startDate;
      if (!startDate) {
        callback(new Error('Please select content.'));
      }
      callback();
    },
    trigger: 'change',
  };
};
let checkModelRules = (profileFormTemp) => {
  return {
    required: true,
    validator: (rule, value, callback) => {
      if (!profileFormTemp.subAgentExpansionModel) {
        callback(new Error('Please select content.'));
      }
      callback();
    },
    trigger: 'change',
  };
};
let checkTemplateRules = (profileFormTemp) => {
  return {
    required: true,
    validator: (rule, value, callback) => {
      if (!profileFormTemp.subSettlementConfigTemplate) {
        callback(new Error('Please select content.'));
      }
      callback();
    },
    trigger: 'change',
  };
};
let checkMaxMinAmount = () => {
  return {
    trigger: 'blur',
    validator: (rule, value, callback) => {
      const amount = value;
      value = delcommafy(amount);
      // -1,000,000,000.00~1,000,000,000.00
      if (value < -1000000000) {
        callback(new Error('Please enter an amount greater than -1,000,000,000.'));
      } else if (value > 1000000000) {
        callback(new Error('Please enter an amount less than 1,000,000,000.'));
      } else {
        callback();
      }
    },
  };
};

let getRevenueSettlementInfoRules = (scope) => {
  return {
    intervalValue: [checkSettlementCycle(scope)],
    startDate: [checkStartDate(scope)],
    settlementMinBalance: [inputRequiredValidateObj, checkMaxMinAmount()],
    targetBalance: [inputRequiredValidateObj, checkMaxMinAmount()],
    triggerBalance: [inputRequiredValidateObj, checkMaxMinAmount()],
  };
};
let getCommissionInfoRules = (profileFormTemp) => {
  return {
    subAgentExpansionModel: [checkModelRules(profileFormTemp)],
    subSettlementConfigTemplate: [checkTemplateRules(profileFormTemp)],
  };
};
export { getRevenueSettlementInfoRules, getCommissionInfoRules };
