<template>
  <div class="panel" v-loading="initLoading">
    <el-row v-if="pageType === 'detail'">
      <el-col>
        <div class="prompt-title">{{ $t('IDC.revenueSettlementInfo.promptTitle') }}</div>
      </el-col>
    </el-row>
    <el-row v-if="!isEditModel && pageType === 'detail'">
      <el-col>
        <el-button
          size="small"
          plain
          @click="setEditModel(true)"
          class="portal-operation-button"
          style="margin-bottom: 20px"
          ><i class="el-icon-edit el-icon--right"></i>{{ $t('common.button.edit') }}
        </el-button>
      </el-col>
    </el-row>
    <el-form
      label-position="top"
      :model="revenueFormData"
      ref="revenueForm"
      class="settlement-form"
      :class="{ viewModel: !isEditModel }"
      :rules="revenueRules"
    >
      <div class="content-wrapper">
        <!-- form items config -->
        <div class="sub-block">
          <el-row :gutter="20">
            <el-col v-bind="colOptions">
              <el-form-item prop="profileName" :label="$t('IDC.revenueSettlementInfo.revenueSettlementProfile')">
                <span v-text="revenueFormData.profileName || '-'" v-if="!isEditModel" />
                <template v-else>
                  <el-select
                    v-model="revenueFormData.profileId"
                    placeholder="Please select"
                    size="small"
                    @click.native="getProfileClick"
                    @change="getProfileChange"
                    clearable
                  >
                    <el-option v-for="item in profileList" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </template>
              </el-form-item>
            </el-col>
            <template v-if="revenueFormData.isShowModel">
              <el-col v-bind="colOptions">
                <el-form-item prop="periodicSwitch" :label="$t('IDC.revenueSettlementInfo.periodicRevenueSettlement')">
                  <span v-if="!isEditModel" v-text="revenueFormData.periodicSwitch === true ? 'ON' : 'OFF'" />
                  <switch-with-text
                    v-else
                    :value.sync="revenueFormData.periodicSwitch"
                    :change="PeriodicHandleChange"
                    :bind="{ size: 'small' }"
                  ></switch-with-text>
                </el-form-item>
              </el-col>
              <el-col v-bind="colOptions" v-if="revenueFormData.periodicSwitch">
                <el-form-item prop="intervalValue" :label="$t('IDC.revenueSettlementInfo.settlementCycle')">
                  <span
                    v-text="
                      revenueFormData.intervalValue +
                      ' ' +
                      getLableToOptions(revenueFormData.intervalTypeCode, settlementCycleUnitList)
                    "
                    v-if="!isEditModel"
                  />
                  <div v-else style="display: flex; gap: 4px">
                    <el-input v-model="revenueFormData.intervalValue" size="small" style="width: 68%"></el-input>
                    <el-select
                      v-model="revenueFormData.intervalTypeCode"
                      placeholder="please select"
                      size="small"
                      style="width: 32%"
                      @change="onTimeUnitChange()"
                    >
                      <el-option
                        v-for="item in settlementCycleUnitList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      >
                      </el-option>
                    </el-select>
                  </div>
                </el-form-item>
              </el-col>
              <el-col v-bind="colOptions" v-if="revenueFormData.periodicSwitch">
                <el-form-item :label="$t('IDC.revenueSettlementInfo.startDate')" prop="startDate">
                  <span v-if="!isEditModel" v-text="getDateFormat(revenueFormData.startDate)"></span>
                  <el-date-picker
                    v-else
                    v-model="revenueFormData.startDate"
                    type="date"
                    size="small"
                    format="dd/MM/yyyy"
                    placeholder="Please select date"
                    value-format="yyyy-MM-dd"
                    :picker-options="startDatePicker"
                    @change="startDateChange"
                    :clearable="false"
                  >
                  </el-date-picker>
                </el-form-item>
              </el-col>
              <el-col v-bind="colOptions" v-if="revenueFormData.periodicSwitch">
                <el-form-item :label="$t('IDC.revenueSettlementInfo.endDate')" prop="endDate">
                  <span v-if="!isEditModel" v-text="getDateFormat(revenueFormData.endDate)"></span>
                  <el-date-picker
                    v-else
                    v-model="revenueFormData.endDate"
                    type="date"
                    size="small"
                    format="dd/MM/yyyy"
                    placeholder="Please select date"
                    value-format="yyyy-MM-dd"
                    @change="endDateChange"
                    :picker-options="endDatePicker"
                    :clearable="false"
                  >
                  </el-date-picker>
                </el-form-item>
              </el-col>
            </template>
            <el-col v-bind="colOptions" v-if="revenueFormData.isShowModel">
              <el-form-item :label="$t('IDC.revenueSettlementInfo.manualInitiation')" prop="manualWsSwitch">
                <span v-if="!isEditModel" v-text="revenueFormData.manualWsSwitch === true ? 'ON' : 'OFF'" />
                <switch-with-text
                  v-else
                  :value.sync="revenueFormData.manualWsSwitch"
                  :bind="{ disabled: true, size: 'small' }"
                ></switch-with-text>
              </el-form-item>
            </el-col>
            <el-col v-bind="colOptions" v-if="revenueFormData.isShowModel">
              <el-form-item :label="$t('IDC.revenueSettlementInfo.scheduledInitiation')" prop="scheduledInitiation">
                <span v-if="!isEditModel" v-text="revenueFormData.autoWsSwitch === true ? 'ON' : 'OFF'" />
                <switch-with-text
                  v-else
                  :value.sync="revenueFormData.autoWsSwitch"
                  :bind="{ disabled: true, size: 'small' }"
                ></switch-with-text>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
        <!-- :key="item.fundingSourceCode + index: -->
        <section class="tabs-container" v-if="revenueFormData.isShowModel" :class="{ create: pageType === 'create' }">
          <el-tabs
            id="tab-pane"
            type="border-card"
            v-model="activeTab"
            @tab-click="handleTabClick"
            style="margin-right: 20px"
          >
            <el-tab-pane
              v-for="(item, index) in revenueFormData.settlementProfileList"
              :key="item['fundingSourceCode'] + item['settlementAccountTypeId'] + item['currency']"
              :label="'Settlement ' + (index + 1)"
              :name="item['fundingSourceCode'] + item['settlementAccountTypeId'] + item['currency']"
            >
              <div class="sub-block">
                <el-row :gutter="20">
                  <el-col v-bind="colOptions">
                    <el-form-item
                      :label="
                        $t('IDC.revenueSettlementInfo.settAccountMinBalance') + showBalanceWithUnit(item.currency)
                      "
                      prop="settlementMinBalance"
                    >
                      <span v-text="item.settlementMinBalance" v-if="!isEditModel" />
                      <el-input
                        v-else
                        v-model="item.settlementMinBalance"
                        clearable
                        @focus="unInputMoney($event)"
                        @blur="inputMoney($event)"
                        size="small"
                      ></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
              </div>
              <el-divider class="divider-margin-2" />
              <div class="sub-block">
                <div class="sub-block__title sub-block__title--normal">
                  {{ $t('IDC.revenueSettlementInfo.revenueSettlementConfiguration') }}
                </div>
                <revenue-table-list
                  :tableData="item.grossRevenueList"
                  :isEditModel="isEditModel"
                  :currency="item.currency"
                ></revenue-table-list>
              </div>
              <div class="sub-block margin-top-45">
                <div class="sub-block__title sub-block__title--normal">
                  {{ $t('IDC.revenueSettlementInfo.liabSettlementConfiguration') }}
                </div>
                <liability-table-list
                  :tableData="item.grossLiabilityList"
                  :isEditModel="isEditModel"
                  :currency="item.currency"
                ></liability-table-list>
              </div>
            </el-tab-pane>
          </el-tabs>
        </section>
      </div>
    </el-form>
    <div class="page-bottom-wrap" v-if="isEditModel && isShowBtn">
      <div>
        <el-button id="cancel" class="cancel-button" size="small" @click="cancel">Cancel </el-button>
        <el-button
          id="submitProduct"
          type="primary"
          size="small"
          :loading="submitLoading"
          @click="submitEvn"
          v-preventReClick
        >
          Submit
        </el-button>
      </div>
    </div>
  </div>
</template>
<script>
import switchWithText from '../../components/switch-with-text.vue';
import {
  queryOrgRevenueProfile,
  queryOrgRevenueTemplateNames,
  getSettlementList,
} from '@/views/identifier-center/api/api-revenue-settlement.js';
import LiabilityTableList from './liability-table-list';
import RevenueTableList from './revenue-table-list';
import { delcommafy, priceFormat } from '@/views/identifier-center/validator/revenue-settlement-info.js';
import { saveOrgRevenueProfile } from '@/views/identifier-center/api/api-revenue-settlement.js';
import { deepClone } from '@/views/identifier-center/pages/organization/utils/common';
import { dealApiRes, getbatchData, transOptions } from '@/views/identifier-center/pages/organization/utils/common.js';
import { getRevenueSettlementInfoRules } from '@/views/identifier-center/validator/revenue-settlement-info.js';
import dayjs from 'dayjs';

export default {
  name: 'RevenueSettlementInfo',
  components: { switchWithText, LiabilityTableList, RevenueTableList },
  props: {
    dataObj: {
      labels: Object,
      default() {
        return {};
      },
    },
    revenueFormData: {
      type: Object,
      default() {
        return {};
      },
    },
    panelActived: {
      type: Boolean,
      default: false,
    },
    isViewMode: {
      type: Boolean,
      default: false,
    },
    isShowBtn: {
      type: Boolean,
      default: false,
    },
    pageType: {
      type: String,
      default: '',
    },
    identityId: {
      type: String,
      default: '',
    },
    assignInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      activeTab: '',
      tabName: 'first',
      revenueRules: getRevenueSettlementInfoRules(this),
      submitLoading: false,
      initLoading: false,
      revenueSettlement: this.revenueFormData,
      isEditModel: !this.isViewMode,
      type: this.pageType,
      operationMode: '',
      profileList: [],
      distributionModelList: [],
      settlementTemplateList: [],
      settlementCycleUnitList: [],
      settlementConfigTemplate: [],
      settlementList: [],
      queryProfileInfo: {},
      remark: '',
      // 
      startDatePicker: {
        disabledDate: (time) => {
          const endDate = this.revenueFormData.endDate;
          if (endDate) {
            return time.getTime() > new Date(endDate).getTime() - 8.64e7;
          }
        },
      },
      // 
      endDatePicker: {
        disabledDate: (time) => {
          const startDate = this.revenueFormData.startDate;
          if (startDate) {
            return (
              time.getTime() < Date.now() || time.getTime() < new Date(startDate).getTime() // - 8.64e7
            );
          } else {
            return time.getTime() < Date.now();
          }
        },
      },
    };
  },
  watch: {
    panelActived: {
      handler(value) {
        if (value) {
          this.initPanel();
        }
      },
      immediate: true,
    },
    revenueSettlement: {
      handler(value) {
        if (value) {
          this.$emit('update:revenueFormData', deepClone(value));
        }
      },
      deep: true,
    },
    'revenueFormData.settlementProfileList': {
      handler(newVal) {
        if (newVal && newVal.length > 0) {
          this.activeTab =
            newVal[0]['fundingSourceCode'] + newVal[0]['settlementAccountTypeId'] + newVal[0]['currency'];
        }
      },
      immediate: true,
    },
    isViewMode: {
      handler(val) {
        this.isEditModel = !val;
      },
      immediate: true,
    },
    pageType(val) {
      this.type = val;
    },
  },
  methods: {
    async initPanel() {
      this.initLoading = true;
      if (this.revenueSettlement.isShowModel) {
        // detail
        this.setEditModel(false);
      }
      const result = await this.getAllInitData();
      if (!result) {
        return;
      }
      // Revenue Settlement Configuration---
      if (!result.profileMap) {
        return;
      }
      const profileEntries = Object.entries(result.profileMap);
      this.profileList = profileEntries.map(([key, value]) => {
        return {
          label: value,
          value: key,
        };
      });

      this.settlementCycleUnitList =
        result.settlementCycleUnitList && result.settlementCycleUnitList.map(transOptions('key', 'value'));
      // 
      if (this.type === 'detail') {
        this.firstQueryOrgProfile(this.identityId);
      }
      this.initLoading = false;
    },

    async getAllInitData() {
      try {
        const result = await getbatchData({
          profileMap: dealApiRes(queryOrgRevenueTemplateNames, 'revenueTemplateNameMap', {}),
          settlementCycleUnitList: dealApiRes(getSettlementList, 'dictList', { type: 'SettlementPeriod' }),
        });
        return result;
      } catch (error) {
        return null;
      }
    },
    // (detail)
    async firstQueryOrgProfile(orgId) {
      this.initLoading = true;
      this.setEditModel(false);
      const profile = await this.getOrgProfile({ orgId });
      this.initLoading = false;
      if (profile) {
        // edit
        this.revenueFormData.isShowModel = true;
        this.operationMode = '2';
        this.setProfileConfig(profile);
      } else {
        this.revenueSettlement.isShowModel = false;
        this.profileList = [];
        this.operationMode = '1';
        this.revenueSettlement.profileId = '';
        this.initLoading = false;
      }
    },

    async getOrgProfile({ orgId, profileId }) {
      let params = {};
      if (orgId) {
        params.orgId = orgId;
      } else {
        params.profileId = profileId;
      }
      const result = await dealApiRes(queryOrgRevenueProfile, 'profile', params);
      return result;
    },
    // (detail)
    getProfileClick() {
      dealApiRes(queryOrgRevenueTemplateNames, 'revenueTemplateNameMap', {}).then((res) => {
        if (!res) {
          return;
        }
        const profileEntries = Object.entries(res);
        this.profileList = profileEntries.map(([key, value]) => {
          return {
            label: value,
            value: key,
          };
        });
      });
    },
    // change(detailcreate)
    async getProfileChange(profileId) {
      // detail
      if (this.type === 'detail') {
        const message =
          'Changing the revenue settlement profile of the organization will delete data of the original revenue settlement profile. Are you sure to continue?';
        this.$confirm(message, 'Confirm', {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          closeOnClickModal: false,
          type: 'warning',
        }).then(async () => {
          this.resetFields();
          this.initLoading = true;
          this.queryProfileData(profileId);
        });
      } else {
        // create no need popup
        this.queryProfileData(profileId);
      }
    },
    async queryProfileData(profileId) {
      if (!profileId) {
        // delete
        if (this.type === 'detail') {
          this.revenueFormData.isShowModel = false;
          this.initLoading = false;
          this.operationMode = '3';
          this.profileList = [];
          this.revenueSettlement = {
            template: false,
            orgId: this.identityId,
          };
          return;
        } else {
          this.revenueFormData.isShowModel = false;
          return;
        }
      }
      this.revenueFormData.isShowModel = true;
      // NEW profile
      const profile = await this.getOrgProfile({ profileId });
      this.initLoading = false;
      if (!profile) {
        return;
      }
      this.setProfileConfig(profile);
    },
    setProfileConfig(profile) {
      const settlementProfile = profile.settlementProfileList.find((item) => item.settlementMinBalance) || {};
      this.revenueSettlement = {
        ...this.revenueFormData,
        ...profile,
        template: false,
        settlementMinBalance: settlementProfile.settlementMinBalance,
        orgId: this.identityId,
      };
    },
    getLableToOptions(val, list) {
      if (!val) {
        return '-';
      }

      if (!list || !list.length) {
        return '';
      }

      if (val === 'Please Select') {
        return '';
      }
      const curName = list.find((item) => item.value === val) || {};
      return curName.label || '-';
    },
    PeriodicHandleChange(val) {
      this.revenueFormData.periodicSwitch = val;
    },
    startDateChange(val) {
      this.revenueFormData.startDate = val;
    },
    endDateChange(val) {
      this.revenueFormData.endDate = val;
    },
    handleTabClick() {},
    cancel() {
      this.$confirm('Are you sure to cancel the operation?', 'Cancel', {
        confirmButtonText: 'Confirm',
        cancelButtonText: 'Cancel',
        closeOnClickModal: false,
        type: 'warning',
      }).then(() => {
        // 
        this.firstQueryOrgProfile(this.identityId);
      });
    },
    // 
    onTimeUnitChange() {
      if (this.revenueFormData.intervalValue) {
        this.$refs['revenueForm'].validateField('intervalValue');
      }
    },
    setStatus() {
      if (!this.revenueFormData.isShowModel && this.operationMode === '1') {
        this.operationMode = '3';
        this.revenueSettlement = {
          template: false,
          orgId: this.identityId,
        };
      }
    },
    excuteRevenueData(tempRevenueData) {
      tempRevenueData.settlementProfileList.forEach((settlement) => {
        settlement.settlementMinBalance = delcommafy(settlement.settlementMinBalance);
        settlement.grossLiabilityList.forEach((grossLiability) => {
          grossLiability.targetBalance = delcommafy(grossLiability.targetBalance);
          grossLiability.triggerBalance = delcommafy(grossLiability.triggerBalance);
        });
        settlement.grossRevenueList.forEach((grossRevenue) => {
          grossRevenue.targetBalance = delcommafy(grossRevenue.targetBalance);
          grossRevenue.triggerBalance = delcommafy(grossRevenue.triggerBalance);
        });
        settlement.liabilityList.forEach((liability) => {
          liability.targetBalance = delcommafy(liability.targetBalance);
          liability.triggerBalance = delcommafy(liability.triggerBalance);
        });
        settlement.revenueList.forEach((revenue) => {
          revenue.targetBalance = delcommafy(revenue.targetBalance);
          revenue.triggerBalance = delcommafy(revenue.triggerBalance);
        });
      });
    },
    submitEvn() {
      this.setStatus();
      if (this.operationMode !== '3') {
        this.$refs['revenueForm'].validateField(['targetBalance', 'triggerBalance'], (errMessage) => {
          if (!errMessage) {
            this.submitEvnData();
          } else {
            return false;
          }
        });
      } else {
        this.submitEvnData();
      }
    },
    submitEvnData() {
      this.$refs['revenueForm'].validate((valid) => {
        if (!valid) {
          return;
        }
        let tempRevenueData = {};
        if (this.operationMode !== '3') {
          tempRevenueData = JSON.parse(JSON.stringify(this.revenueFormData));
          this.excuteRevenueData(tempRevenueData);
        } else {
          tempRevenueData = {
            template: false,
            orgId: this.identityId,
          };
        }
        this.submitLoading = true;
        this.$confirm('Are you sure to submit it?', 'Tips', {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          closeOnClickModal: false,
        }).then(() => {
          let params = {
            // 1 --   2 --   3 -- 
            operationMode: this.operationMode,
            profile: tempRevenueData,
          };
          saveOrgRevenueProfile(params)
            .then((res) => {
              if (res.header && res.header.success) {
                this.$modal.msgSuccess('orgRevenueSettlementProfile submitted successfully.');
                this.setEditModel(false);
                // 
                this.firstQueryOrgProfile(this.identityId);
              }
            })
            .finally(() => {
              this.submitLoading = false;
            });
        });
      });
    },
    setEditModel(val) {
      this.isEditModel = val;
      this.profileList = [];
      if (!this.revenueSettlement.profileId) {
        this.revenueFormData.isShowModel = false;
        return;
      }
      this.revenueFormData.profileId = '';
      this.revenueFormData.isShowModel = true;
    },
    resetFields() {
      this.$refs.revenueForm.resetFields();
    },
    // 
    inputMoney(el) {
      let temp = null;
      if (el.target.value === '0') {
        temp = Number(el.target.value);
      } else {
        temp = Number(el.target.value) || null;
      }
      el.target.value = priceFormat(temp);
    },
    // 
    unInputMoney(el) {
      if (el.target.value) {
        this.revenueFormData.settlementMinBalance = delcommafy(el.target.value);
      } else {
        this.revenueFormData.settlementMinBalance = null;
      }
    },
    showBalanceWithUnit(currency) {
      return currency === 'USD' ? '($)' : `(${currency})`;
    },
    getDateFormat(date) {
      return dayjs(date, 'YYYY-MM-DD').format('DD/MM/YYYY');
    },
  },
  activated() {
    this.initPanel();
  },
};
</script>
