import request from '@/common/utils/request';
import { buildUrlWithObj } from '@/common/utils/url-utils';

// 
export function getOrganizationList(data) {
  return request({
    url: buildUrlWithObj('/fintech/v1/organization/list', data),
    method: 'get',
  });
}
// 
export function queryOrgListByPage(data) {
  if (data.pageNo) {
    data.pageIndex = data.pageNo;
    delete data.pageNo;
  }
  return request({
    url: '/fintech/v1/organization/queryOrgListByPage',
    method: 'POST',
    data,
  });
}

