<template>
  <div class="container">
    <organization-basic-info :header="header" :basic-info="basicInfo"></organization-basic-info>

    <section class="tabs-container">
      <el-tabs v-model="tabName">
        <el-tab-pane :label="$t('IDC.groupDetail.label.AccountInfo')" name="first" :lazy="true">
          <org-account :identity-id="basicInfo.organizationId" :is-top-organization="basicInfo.isTopOrganization" />
        </el-tab-pane>
        <el-tab-pane :label="$t('IDC.organization.product')" name="second" :lazy="true">
          <product-info :identity-id="basicInfo.organizationId"></product-info>
        </el-tab-pane>
        <el-tab-pane :label="$t('IDC.groupDetail.label.KYCInfo')" name="third" :lazy="true">
          <div class="org-kyc-info">
            <div class="tips">KYC information of an organization. The field marked with * is mandatory.</div>
            <kyc-form ref="kycForm" mode="edit" :identityId="identityId" identityClassify="org" />
          </div>
        </el-tab-pane>
        <el-tab-pane :label="$t('IDC.revenueSettlementInfo.revenueSettlementConfig')" name="fourth" :lazy="true">
          <revenue-settlement-info
            :panelActived="tabName === 'fourth'"
            :revenueFormData.sync="revenueFormData"
            :identityId="basicInfo.organizationId"
            :pageType="pageType"
            :isShowBtn="isShowBtn"
            ref="revenueForm"
          ></revenue-settlement-info>
        </el-tab-pane>
      </el-tabs>
    </section>
  </div>
</template>

<script>
import OrganizationBasicInfo from './components/basic-info';
import ProductInfo from '@/views/identifier-center/pages/organization/detail/components/product-info.vue';
import RevenueSettlementInfo from '@/views/identifier-center/pages/organization/detail/components/revenue-settlement-info.vue';
import { getOrganizationDetail, queryOrgCommiAgreement } from '@/views/identifier-center/api/api-organization';
import { queryOrganizationDetail } from '@/views/identifier-center/api/my-organization';
import OrgAccount from '@/views/identifier-center/pages/account';
import KycForm from '@/components/kyc-common/kyc-form';

export default {
  name: 'OrganizationDetail',

  components: {
    OrgAccount,
    OrganizationBasicInfo,
    ProductInfo,
    RevenueSettlementInfo,
    KycForm,
  },
  props: {
    organizationId: {
      type: String,
      default: '',
    },
  },
  provide() {
    return {
      orgDetailView: this,
    };
  },
  data() {
    return {
      tabName: 'first',
      id: '',
      identityId: '',
      header: {
        title: '',
      },
      basicInfo: {},
      status: [],
      roles: [],
      pageType: 'detail',
      isShowBtn: true,
      revenueFormData: {
        profileId: 'Please Select',
        profileName: '',
        periodicSwitch: false,
        intervalValue: '',
        intervalTypeCode: '',
        startDate: '',
        endDate: '',
        orgId: '',
        manualWsSwitch: false,
        autoWsSwitch: false,
        settlementMinBalance: '',
        editableTabsValue: '0',
        isShowModel: true,
      },
      payBillCheckRule: {},
    };
  },
  created() {
    this.init();
  },

  methods: {
    async init() {
      let identityId = '';
      if (this.organizationId) {
        identityId = this.organizationId;
      } else {
        const routerParams = JSON.parse(decodeURIComponent(this.$route.query.data));
        identityId = routerParams.identityId;
      }
      if (!identityId) {
        return;
      }
      this.loadOrganizationInfo(identityId);
    },
    async loadOrganizationInfo(identityId) {
      this.identityId = identityId;
      let data = await getOrganizationDetail({ identityId });
      this.payBillCheckRule = data.payBillCheckRule;
      const basicInfo = data.organizationInfo || {};

      if (basicInfo.organizationId) {
        let commission = await this.queryOrgCommiAgreement(basicInfo.organizationId);
        basicInfo.distributionModelName = commission.distributionModelName;
        basicInfo.distributionModelId = commission.distributionModelId;
        basicInfo.settleConfigTemplateId = commission.settleConfigTemplateId;
        basicInfo.settleConfigTemplateName = commission.settleConfigTemplateName;
        //  
        const loginedAccoutOrgInfo = await this.getLoginedAccoutOrgInfo();
        if (loginedAccoutOrgInfo) {
          basicInfo.loginedAccoutIsTopOrg = loginedAccoutOrgInfo.isTopOrganization;
          basicInfo.loginedAccoutOrgId = loginedAccoutOrgInfo.organizationId;
        }
      }
      this.basicInfo = basicInfo;

      this.header.title = `${this.basicInfo.shortCode}-${this.basicInfo.organizationName}`;
    },
    queryOrgCommiAgreement(organizationId) {
      return queryOrgCommiAgreement({ orgId: organizationId })
        .then((res) => {
          if (res.header && res.header.success) {
            return res;
          }
          return {};
        })
        .catch(() => ({}));
    },
    getLoginedAccoutOrgInfo() {
      return queryOrganizationDetail({ identityId: '' })
        .then((res) => {
          if (res.header && res.header.success) {
            let organizationInfo = res.organizationInfo;
            if (organizationInfo) {
              return organizationInfo;
            }
            return null;
          }
        })
        .catch((err) => null);
    },

    resetFields() {},
  },
};
</script>

