<template>
  <div class="container">
    <section>
      <div class="content-container">
        <section class="header p-l-r-20">
          <div class="icon-organization">
            <svg-icon class="org-icon" icon-class="organization"></svg-icon>
          </div>
          <div class="content-container flex flex-col flex-jus-between">
            <div class="content-title">{{ header.title }}</div>
            <div class="content-sub-title">
              {{ $t('IDC.organizationDetail.identityStatus') }}
              <span class="identity-status">{{ getIdentityStatusName(basicInfo.status) || '-' }}</span>
              <el-link
                :underline="false"
                icon="el-icon-edit"
                @click="editIdentityStatus"
                v-if="isAllowedEdit && !isClosed"
              ></el-link>
            </div>
          </div>
        </section>
        <section class="p-l-r-20">
          <el-row :gutter="20">
            <!-- Parent Short Code-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">{{ $t('IDC.organization.parentShortCode') }}</div>
              </el-row>
              <el-row>
                <div class="content content-item">{{ basicInfo.parentShortCode || '-' }}</div>
              </el-row>
            </el-col>

            <!-- Identity Model-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">{{ $t('IDC.organization.identityModel') }}</div>
              </el-row>
              <el-row>
                <div class="content content-item">{{ getSubAgentModelName(basicInfo.identityModel) || '-' }}</div>
              </el-row>
            </el-col>

            <!-- Hierarchy Level-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">{{ $t('IDC.organization.hierarchyLevel') }}</div>
              </el-row>
              <el-row>
                <div class="content content-item">{{ basicInfo.hierLevel || '-' }}</div>
              </el-row>
            </el-col>

            <!-- Top Organization-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">{{ $t('IDC.organization.topOrganization') }}</div>
              </el-row>
              <el-row>
                <div class="content content-item">{{ basicInfo.topOrganizationShortCode || '-' }}</div>
              </el-row>
            </el-col>

            <!-- Top Organization Name-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">{{ $t('IDC.organization.topOrganizationName') }}</div>
              </el-row>
              <el-row>
                <div class="content content-item">{{ basicInfo.topOrganizationName || '-' }}</div>
              </el-row>
            </el-col>

            <!-- Organization Name-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">
                  {{ $t('IDC.organizationDetail.organizationName') }}
                </div>
              </el-row>
              <el-row>
                <div class="content content-item">
                  {{ basicInfo.organizationName || '-' }}
                  <el-link
                    :underline="false"
                    icon="el-icon-edit"
                    @click="editOrganizationName"
                    v-if="!isClosed && !isDormant && !isNew"
                  ></el-link>
                </div>
              </el-row>
            </el-col>
            <!-- Short Code-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">
                  {{ $t('IDC.organizationDetail.shortCode') }}
                </div>
              </el-row>
              <el-row>
                <div class="content content-item">{{ basicInfo.shortCode || '-' }}</div>
              </el-row>
            </el-col>

            <!-- Organization Code-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">{{ $t('IDC.organizationDetail.organizationCode') }}</div>
              </el-row>
              <el-row>
                <div class="content content-item">
                  {{ basicInfo.organizationCode || '-' }}
                </div>
              </el-row>
            </el-col>

            <!-- Segment-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">
                  {{ $t('IDC.organizationDetail.segment') }}
                </div>
              </el-row>
              <el-row>
                <div class="content content-item">{{ basicInfo.segment || '-' }}</div>
              </el-row>
            </el-col>

            <!-- Charge Profile-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">
                  {{ $t('IDC.organizationDetail.chargeProfile') }}
                </div>
              </el-row>
              <el-row>
                <div class="content content-item">
                  <span
                    :class="{ 'content-item--has-underline': basicInfo.chargeProfileName }"
                    @click="goToDetailPage(PROFILE_TYPES.CHARGE)"
                    >{{ basicInfo.chargeProfileName || '-' }}</span
                  >
                  <el-link :underline="false" icon="el-icon-edit" @click="editChargeProfile" v-if="!isClosed"></el-link>
                </div>
              </el-row>
            </el-col>
            <!-- Rule Profile-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">
                  {{ $t('IDC.organizationDetail.ruleProfile') }}
                </div>
              </el-row>
              <el-row>
                <div class="content content-item">
                  <span
                    :class="{ 'content-item--has-underline': basicInfo.ruleProfileName }"
                    @click="goToDetailPage(PROFILE_TYPES.RULE)"
                    >{{ basicInfo.ruleProfileName || '-' }}</span
                  >
                  <!-- DTS2023010613076 -->
                  <el-link
                    :underline="false"
                    icon="el-icon-edit"
                    @click="editRuleProfile"
                    v-if="false && !isClosed"
                  ></el-link>
                </div>
              </el-row>
            </el-col>
            <!-- Trust Level-->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">{{ $t('IDC.organizationDetail.trustLevel') }}</div>
              </el-row>
              <el-row>
                <div class="content content-item">
                  {{ basicInfo.trustLevelName || '-' }}
                </div>
              </el-row>
            </el-col>
            <!-- Registration Date -->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">
                  {{ $t('IDC.organizationDetail.registrationDate') }}
                </div>
              </el-row>
              <el-row>
                <div class="content content-item">{{ basicInfo.createTime | localTimeFormat('DD/MM/YYYY') }}</div>
              </el-row>
            </el-col>
            <!-- Infrequency User -->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">
                  {{ $t('IDC.organizationDetail.infrequencyUser') }}
                </div>
              </el-row>
              <el-row>
                <div class="content content-item">{{ basicInfo.infrequencyUser || '-' }}</div>
              </el-row>
            </el-col>
            <!-- Registration Channel -->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">
                  {{ $t('IDC.organizationDetail.registrationChannel') }}
                </div>
              </el-row>
              <el-row>
                <div class="content content-item">{{ basicInfo.registrationChannel || '-' }}</div>
              </el-row>
            </el-col>
            <!-- Commission Distribution Agreement -->
            <el-col v-bind="colOptions">
              <el-row>
                <div class="content content-item-label">
                  {{ $t('IDC.organization.commissionAgreement') }}
                </div>
              </el-row>
              <el-row>
                <div class="content content-item">
                  <span>{{ basicInfo.distributionModelName || '-' }}</span>
                  <el-link
                    :underline="false"
                    icon="el-icon-edit"
                    @click="editCommiAgreement"
                    v-if="!isClosed && hasEditPermission && isAggregator"
                  ></el-link>
                </div>
              </el-row>
            </el-col>
            <!-- Commission Settlement Configuration Template -->
            <el-col :span="10">
              <el-row>
                <div class="content content-item-label">{{ $t('IDC.organization.commissionTemplate') }}</div>
              </el-row>
              <el-row>
                <div class="content content-item">
                  {{ basicInfo.settleConfigTemplateName || '-' }}
                  <el-link
                    :underline="false"
                    icon="el-icon-edit"
                    @click="editSettlementTemplate"
                    v-if="!isClosed && hasEditPermission && !basicInfo.isTopOrganization"
                  ></el-link>
                </div>
              </el-row>
            </el-col>
          </el-row>
        </section>
      </div>
    </section>
    <content-item-edit-dialog
      :visible.sync="showDialog"
      :config="dialogConfig"
      :submitLoading="submitLoading"
      :validation-rules="organizationRules"
      :close-on-click-modal="false"
      @on-submit="onDialogSubmit"
    >
    </content-item-edit-dialog>
  </div>
</template>

<script>
import ContentItemEditDialog from '@/views/identifier-center/pages/organization/detail/components/content-item-edit-dialog';
import { BasicInfoRule } from '@/views/identifier-center/validator/organization-info';
import {
  changeOrganizationName,
  getOrganizationStatus,
  organizationChangeStatus,
  queryChargeProfileList,
  queryRuleProfileList,
  updateChargeProfile,
  updateRuleProfile,
} from '@/views/identifier-center/api/api-organization';
import { queryCommissionCfg, updateOrgCommiAgreement } from '@/views/identifier-center/api/api-revenue-settlement';
import {
  getIdentityStatusName,
  getSubAgentModelName,
  IDENTITY_MODEL,
  IDENTITY_STATUS_TYPE,
  isAllowedEdit,
} from '@/views/identifier-center/pages/organization/utils/organizatoin-enums';
import {
  dealApiRes,
  getLabelFromOptions,
  transOptions,
} from '@/views/identifier-center/pages/organization/utils/common.js';

const PROFILE_TYPES = {
  CHARGE: 'chargeProfile',
  RULE: 'ruleProfile',
};
const TEMPLARW_TYPES = {
  AGREEMENT: '1',
  CONFIG: '2',
};

export default {
  name: 'BasicInfo',
  components: {
    ContentItemEditDialog,
  },
  inject: ['orgDetailView'],
  props: {
    header: {
      type: Object,
      default: () => {},
    },
    basicInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      PROFILE_TYPES,
      showDialog: false,
      submitLoading: false,
      dialogConfig: {},
      organizationRules: {},
      status: [],
      ruleProfiles: [],
      chargeProfiles: [],
      commiAgreements: [],
      settleConfigTemplates: [],
    };
  },

  computed: {
    isClosed() {
      return this.basicInfo.status === IDENTITY_STATUS_TYPE.CLOSED;
    },
    isDormant() {
      return this.basicInfo.status === IDENTITY_STATUS_TYPE.DORMANT;
    },
    isNew() {
      return this.basicInfo.status === IDENTITY_STATUS_TYPE.NEW;
    },
    isAllowedEdit() {
      const status = this.basicInfo.status;
      return isAllowedEdit(status);
    },

    hasEditPermission() {
      const loginedAccoutIsTopOrg = this.basicInfo.loginedAccoutIsTopOrg;
      const isBeloongstoLoginedOrg = this.basicInfo.loginedAccoutOrgId === this.basicInfo.topOrganizationId;
      return loginedAccoutIsTopOrg && isBeloongstoLoginedOrg;
    },
    isAggregator() {
      // 
      const isAggregator = this.basicInfo.identityModel === IDENTITY_MODEL.AGGREGATOR;
      return isAggregator;
    },
    isCentralOwned() {
      // 
      const isCentralOwned = this.basicInfo.identityModel === IDENTITY_MODEL.CENTRAL_OWNED;
      return isCentralOwned;
    },
  },

  created() {
    this.init();
  },

  methods: {
    init() {
      this.organizationRules = BasicInfoRule();
    },
    async loadOrganizationStatus(status) {
      const res = await getOrganizationStatus({ statusCode: status });
      let data = res.statusList;
      if (data && data.length) {
        this.status.length = 0;
        data = data.filter((item) => item.code !== status);
        this.status.push(
          ...data.map((e) => {
            return {
              value: e.code,
              label: e.name,
            };
          })
        );
      }
    },
    async loadRuleProfiles() {
      const param = {
        trustlevel: this.basicInfo.trustLevel,
        identityType: this.basicInfo.identityType,
        identitySubtype: this.basicInfo.identitySubType,
      };

      let data = await dealApiRes(queryRuleProfileList, 'dics', param);
      this.ruleProfiles.length = 0;
      if (data && data.length) {
        data = data.map(transOptions('code', 'name', true));
        data.forEach((item) => this.ruleProfiles.push(item));
      }
    },
    async loadChargeProfiles() {
      const param = {
        trustlevel: this.basicInfo.trustLevel,
        identityType: this.basicInfo.identityType,
        identitySubtype: this.basicInfo.identitySubType,
      };

      let data = await dealApiRes(queryChargeProfileList, 'dics', param);
      data = data || [];
      this.chargeProfiles.length = 0;
      const emptyItem = {
        name: this.$t('common.placeholder.select'),
        code: null,
      };
      data.unshift(emptyItem);
      data = data.map(transOptions('code', 'name', true));
      data.forEach((item) => this.chargeProfiles.push(item));
    },

    async loadCommiAgreement() {
      const params = {
        templateType: TEMPLARW_TYPES.AGREEMENT,
        pageSize: null,
        pageNo: null,
      };
      let data = await dealApiRes(queryCommissionCfg, 'commissionConfigs', params);
      this.commiAgreements.length = 0;
      if (data && data.length) {
        data = data.map(transOptions('templateId', 'name'));
        this.commiAgreements.push(...data);
      }
    },
    async loadSettleConfigTemplates() {
      const params = {
        templateType: TEMPLARW_TYPES.CONFIG,
        pageSize: null,
        pageNo: null,
      };
      let data = await dealApiRes(queryCommissionCfg, 'commissionConfigs', params);
      this.settleConfigTemplates.length = 0;
      if (data && data.length) {
        data = data.map(transOptions('templateId', 'name'));
        this.settleConfigTemplates.push(...data);
      }
    },
    editCommiAgreement() {
      this.dialogConfig = {
        title: 'Edit Commission Distribution Agreement',
        label: 'Current Commission Distribution Agreement',
        value: this.basicInfo.distributionModelName,
        prop: 'distributionTemplate',
        properties: [
          {
            key: 'distributionTemplate',
            default: this.basicInfo.distributionModelId,
            label: 'New Commission Distribution Agreement',
            type: 'select',
            options: this.commiAgreements,
          },
          {
            key: 'remark',
            default: '',
            label: 'Remark',
            type: 'textarea',
          },
        ],
      };

      this.loadCommiAgreement().then(() => {
        this.showDialog = true;
      });
    },
    editSettlementTemplate() {
      this.dialogConfig = {
        title: 'Edit Commission Settlement Configuration Template',
        label: 'Current Commission Settlement Configuration Template',
        value: this.basicInfo.settleConfigTemplateName,
        prop: 'settleConfigTemplate',
        properties: [
          {
            key: 'settleConfigTemplate',
            default: this.basicInfo.settleConfigTemplateId,
            label: 'New Commission Settlement Configuration Template',
            type: 'select',
            options: this.settleConfigTemplates,
          },
          {
            key: 'remark',
            default: '',
            label: 'Remark',
            type: 'textarea',
          },
        ],
      };

      this.loadSettleConfigTemplates().then(() => {
        this.showDialog = true;
      });
    },
    editIdentityStatus() {
      const dialogConfig = {
        title: this.$t('IDC.orgOperator.editStatus'),
        label: this.$t('IDC.orgOperator.currentStatus'),
        value: this.getIdentityStatusName(this.basicInfo.status),
        prop: 'identityStatus',
        properties: [
          {
            key: 'identityStatus',
            default: this.basicInfo.status,
            useDefult: true,
            label: this.$t('IDC.orgOperator.newStatus'),
            type: 'select',
            options: this.status,
          },
          {
            key: 'remark',
            default: '',
            label: this.$t('common.label.remark'),
            type: 'textarea',
          },
        ],
      };

      this.loadOrganizationStatus(this.basicInfo.status).then(() => {
        if (this.status && this.status.length) {
          const firtOption = this.status[0];
          dialogConfig.properties[0].default = firtOption.value;
        }
        this.dialogConfig = dialogConfig;
        this.showDialog = true;
      });
    },
    editRuleProfile() {
      this.dialogConfig = {
        title: this.$t('IDC.groupDetail.label.EditRuleProfile'),
        label: this.$t('IDC.groupDetail.label.currentRuleProfile'),
        value: this.basicInfo.ruleProfileName,
        prop: 'ruleProfile',
        properties: [
          {
            key: 'ruleProfile',
            default: this.basicInfo.ruleProfileId,
            label: this.$t('IDC.groupDetail.label.RuleProfile'),
            type: 'select',
            options: this.ruleProfiles,
          },
          {
            key: 'remark',
            default: '',
            label: this.$t('common.label.remark'),
            type: 'textarea',
          },
        ],
      };
      this.loadRuleProfiles().then(() => {
        this.showDialog = true;
      });
    },
    editChargeProfile() {
      this.dialogConfig = {
        title: this.$t('IDC.organization.editChargeProfile'),
        label: 'Current Charge Profile',
        value: this.basicInfo.chargeProfileName,
        prop: 'chargeProfile',
        properties: [
          {
            key: 'chargeProfile',
            default: this.basicInfo.chargeProfileId,
            label: this.$t('IDC.organization.table.chargeProfile'),
            type: 'select',
            options: this.chargeProfiles,
          },
          {
            key: 'remark',
            default: '',
            label: this.$t('common.label.remark'),
            type: 'textarea',
          },
        ],
      };
      this.loadChargeProfiles().then(() => {
        this.showDialog = true;
      });
    },
    editOrganizationName() {
      this.dialogConfig = {
        title: this.$t('IDC.organization.editOrganizationName'),
        label: 'Current Organization Name',
        value: this.basicInfo.organizationName,
        prop: 'organizationName',
        newOrganizationName: '',
        properties: [
          {
            key: 'newOrganizationName',
            default: '',
            label: this.$t('IDC.organization.newOrganizationName'),
            type: 'input',
            validateStatus: 'el-form-item-pending',
            itemClass: 'el-form-item-no-error',
            eleClass: 'success-icon',
            elementBind: {
              'suffix-icon': 'el-icon-success',
            },
          },
          {
            key: 'remark',
            default: '',
            label: this.$t('common.label.remark'),
            type: 'textarea',
          },
        ],
      };
      this.showDialog = true;
    },
    onDialogSubmit(type, data) {
      switch (type) {
        case 'identityStatus':
          this.changeOrganizationStatus(data);
          break;
        case 'ruleProfile':
          this.changeRuleProfile(data).then();
          break;
        case 'chargeProfile':
          this.changeChargeProfile(data).then();
          break;
        case 'organizationName':
          this.changeOrganizationName(data);
          break;
        case 'distributionTemplate':
          this.changeDistributionTemplate(data);
          break;
        case 'settleConfigTemplate':
          this.changeSettleConfigTemplate(data);
          break;
      }
    },
    async changeSettleConfigTemplate(data) {
      let param = {};
      param.orgId = this.basicInfo.organizationId;
      param.oldSettleConfigTemplateId = this.basicInfo.settleConfigTemplateId;
      param.newSettleConfigTemplateId = data.settleConfigTemplate;
      param.topIdentityId = this.basicInfo.topOrganizationId;
      param.remark = data.remark;

      try {
        this.submitLoading = true;
        let res = await updateOrgCommiAgreement(param);
        this.submitLoading = false;
        if (res && res.header && res.header.success) {
          this.$modal.msgSuccess('Edit Commission Settlement Configuration Template success.');
          this.showDialog = false;
          this.refreshBasicInfo();
        }
      } catch (e) {}
    },
    async changeDistributionTemplate(data) {
      let param = {};
      param.orgId = this.basicInfo.organizationId;
      param.oldDistributionModelId = this.basicInfo.distributionModelId;
      param.newDistributionModelId = data.distributionTemplate;
      param.topIdentityId = this.basicInfo.topOrganizationId;
      param.remark = data.remark;

      try {
        this.submitLoading = true;
        let res = await updateOrgCommiAgreement(param);
        this.submitLoading = false;
        if (res && res.header && res.header.success) {
          this.basicInfo.distributionModelId = param.newDistributionModelId;
          this.basicInfo.distributionModelName = getLabelFromOptions(
            this.commiAgreements,
            param.newDistributionModelId
          );
          this.$modal.msgSuccess('Edit Commission Distribution Agreement success.');
          this.showDialog = false;
        }
      } catch (e) {}
    },

    async changeOrganizationName(data) {
      let param = {};
      param.organizationName = data.newOrganizationName;
      param.receiverIdentityId = this.basicInfo.organizationId;
      param.remark = data.remark;
      this.showDialog = false;

      try {
        this.submitLoading = true;
        let res = await changeOrganizationName(param);
        this.submitLoading = false;
        if (res && res.header && res.header.success) {
          this.basicInfo.organizationName = param.organizationName;
          if (res.taskId) {
            this.$message.success('The request must be approved by another operator.');
          } else {
            this.$modal.msgSuccess('Organization Name changed success.');
          }
          this.header.title = `${this.basicInfo.shortCode}-${this.basicInfo.organizationName}`;
          this.showDialog = false;
        }
      } catch (e) {}
    },
    async changeOrganizationStatus(data) {
      let param = {};
      param.oldStatus = this.basicInfo.status;
      param.newStatus = data.identityStatus;
      param.receiverIdentityId = this.basicInfo.organizationId;
      param.remark = data.remark;
      try {
        this.submitLoading = true;
        let res = await organizationChangeStatus(param);
        this.submitLoading = false;
        if (res && res.header && res.header.success) {
          this.basicInfo.status = data.identityStatus;
          if (res.taskId) {
            this.$message.success('The request must be approved by another operator.');
          } else {
            this.$modal.msgSuccess('Identity status changed success.');
          }
          this.showDialog = false;
        }
      } catch (e) {}
    },
    async changeRuleProfile(data) {
      let param = {};
      param.receiverIdentityId = this.basicInfo.organizationId;
      param.ruleProfileId = data.ruleProfile;
      param.trustLevel = this.basicInfo.trustLevelName;
      param.remark = data.remark;
      try {
        this.submitLoading = true;
        let res = await updateRuleProfile(param);
        this.submitLoading = false;
        if (res && res.header && res.header.success) {
          this.basicInfo.ruleProfileId = data.ruleProfile;
          this.basicInfo.ruleProfileName = getLabelFromOptions(this.ruleProfiles, data.ruleProfile);
          this.$modal.msgSuccess('Rule Profile changed success.');
          this.showDialog = false;
          this.ruleProfiles.length = 0;
        }
      } catch (e) {}
    },
    async changeChargeProfile(data) {
      let param = {};
      param.receiverIdentityId = this.basicInfo.organizationId;
      param.chargeProfileId = data.chargeProfile;
      param.trustLevel = this.basicInfo.trustLevelName;
      param.remark = data.remark;

      try {
        this.submitLoading = true;
        let res = await updateChargeProfile(param);
        this.submitLoading = false;
        if (res && res.header && res.header.success) {
          this.basicInfo.chargeProfileId = data.chargeProfile;
          this.basicInfo.chargeProfileName = getLabelFromOptions(this.chargeProfiles, data.chargeProfile);
          this.$modal.msgSuccess('Charge Profile changed success.');
          this.showDialog = false;
          this.chargeProfiles.length = 0;
        }
      } catch (e) {}
    },
    goToDetailPage(profileType) {
      let params = {
        id: null,
      };
      let path = '';
      if (PROFILE_TYPES.CHARGE === profileType) {
        path = 'chargeProfile';
        params.id = this.basicInfo.chargeProfileId;
      }
      if (PROFILE_TYPES.RULE === profileType) {
        path = 'ruleProfile';
        params.id = this.basicInfo.ruleProfileId;
      }
      if (params.id) {
        this.openNewWindow({ path: path, query: { data: encodeURIComponent(JSON.stringify(params)) } });
      }
    },
    getIdentityStatusName(status) {
      return getIdentityStatusName(status);
    },
    getSubAgentModelName(status) {
      return getSubAgentModelName(status);
    },
    refreshBasicInfo() {
      if (this.orgDetailView) {
        this.orgDetailView.init();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  background-color: $base-menu-background;
  border-radius: 4px;
