<script>
export default {
  name: 'SwitchWithText',
  props: {
    value: {
      type: Boolean,
      default: () => true,
    },
    change: {
      Type: Function,
      default: () => {},
    },
    bind: {
      type: Object,
      default: () => ({}),
    },
    activeText: {
      type: String,
      default: () => 'ON',
    },
    inactiveText: {
      type: String,
      default: () => 'OFF',
    },
    position: {
      type: String,
      default: () => 'right',
      validator: function (value) {
        return ['left', 'right', 'leftRight'].includes(value);
      },
    },
  },

  data() {
    return {
      val: this.value,
    };
  },

  watch: {
    value(val) {
      this.val = val;
    },
  },

  methods: {
    getStatusText(val) {
      if (val) {
        return this.activeText;
      }
      return this.inactiveText;
    },
    changeHandle(val) {
      this.$emit('update:value', val);
      this.change(val);
    },
  },

  render(h) {
    const textProps = {};

    if (this.position === 'right') {
      textProps.activeText = this.getStatusText(this.val);
    }

    if (this.position === 'left') {
      textProps.inactiveText = this.getStatusText(this.val);
    }

    if (this.position === 'leftRight') {
      textProps.inactiveText = this.inactiveText;
      textProps.activeText = this.activeText;
    }

    const classList = [];
    let styleObj = {};

    if (this.bind && this.bind.class) {
      classList.push(this.bind.class);
    }
    if (this.bind && this.bind.style) {
      styleObj = {
        ...styleObj,
        ...this.bind.style,
      };
    }

    const options = {
      class: classList,
      style: styleObj,
      props: {
        ...this.bind,
        ...textProps,
        value: this.val,
      },
      on: {
        change: this.changeHandle,
      },
    };
    return h('el-switch', options);
  },
};
</script>
