<template>
  <el-table :data="tableData" border style="width: 100%" :row-style="{ height: '56px' }">
    <el-table-column
      :label="$t('IDC.revenueSettlementInfo.revenueAccount')"
      prop="accountTypeName"
      min-width="168px"
    ></el-table-column>
    <el-table-column
      :label="$t('IDC.revenueSettlementInfo.targetBalance') + showBalanceWithUnit(currency)"
      prop="targetBalance"
      required
      min-width="158px"
      :render-header="requiredStar"
    >
      <template v-slot="scope">
        <div class="columns">
          <el-tooltip
            :content="getContentTips(scope.row, 'target')"
            effect="light"
            placement="top-start"
            :arrow-offset="16"
            popper-class="text-content-error-tooltip"
            :disabled="scope.row.targetDisabled"
          >
            <el-form-item prop="targetBalance" :rules="isEditModel ? checkRules(scope.row, 'target') : {}">
              <el-input
                size="small"
                type="text"
                style="width: 100%"
                :disabled="!isEditModel ? true : false"
                v-model="scope.row.targetBalance"
                @focus="unInputMoney($event, scope, 'target')"
                @blur="inputMoney($event, scope, 'target')"
              ></el-input>
            </el-form-item>
          </el-tooltip>
        </div>
      </template>
    </el-table-column>
    <el-table-column
      :label="$t('IDC.revenueSettlementInfo.settlementTriggerBalance') + showBalanceWithUnit(currency)"
      prop="triggerBalance"
      min-width="208px"
      :render-header="addRequiredStar"
    >
      <template v-slot="scope">
        <div class="columns">
          <el-tooltip
            :content="getContentTips(scope.row, 'trigger')"
            effect="light"
            placement="top-start"
            :arrow-offset="16"
            popper-class="text-content-error-tooltip"
            :disabled="scope.row.triggerDisabled"
          >
            <el-form-item prop="triggerBalance" :rules="checkRules(scope.row, 'trigger')">
              <el-switch
                style="margin-right: 10px"
                v-model="scope.row.triggerSwitch"
                :active-text="scope.row.triggerSwitch ? 'ON' : 'OFF'"
                :disabled="!isEditModel ? true : false"
                @change="triggerHandleChange(scope.row, scope.$index)"
              ></el-switch>
              <el-input
                v-if="scope.row.triggerSwitch"
                size="small"
                type="text"
                style="width: 72%"
                :disabled="!isEditModel ? true : false"
                v-model="scope.row.triggerBalance"
                @focus="unInputMoney($event, scope, 'trigger')"
                @blur="inputMoney($event, scope, 'trigger')"
              ></el-input>
            </el-form-item>
          </el-tooltip>
        </div>
      </template>
    </el-table-column>
    <el-table-column
      :label="$t('IDC.revenueSettlementInfo.maxBalanceTrigger')"
      prop="maxMinBalanceTrigger"
      min-width="100px"
    >
      <template v-slot="scope">
        <div class="columns">
          <el-switch
            style="width: 95px"
            v-model="scope.row.maxMinBalanceTrigger"
            :active-text="scope.row.maxMinBalanceTrigger ? 'ON' : 'OFF'"
            :disabled="!isEditModel ? true : false"
          ></el-switch>
        </div>
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
import { delcommafy, priceFormat } from '@/views/identifier-center/validator/revenue-settlement-info.js';
export default {
  name: 'TblProductService',
  props: {
    tableData: {
      type: Array,
      default: () => [],
    },
    isEditModel: {
      type: Boolean,
      default: false,
    },
    dateRange: {
      type: Object,
      default: () => {},
    },
    currency: {
      type: String,
      default: '',
    },
  },
  watch: {},
  data() {
    return {
      services: [],
      amount: '',
      hasTriggerSwitch: false,
    };
  },
  computed: {},
  methods: {
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    // 
    inputMoney(el, scope, type) {
      let temp = null;
      if (el.target.value === '0') {
        temp = Number(el.target.value);
      } else {
        temp = Number(el.target.value) || null;
      }
      if (type === 'trigger') {
        scope.row.triggerBalance = priceFormat(temp);
      } else {
        scope.row.targetBalance = priceFormat(temp);
      }
    },
    // 
    unInputMoney(el, scope, type) {
      if (el.target.value) {
        if (type === 'trigger') {
          scope.row.triggerBalance = delcommafy(el.target.value);
        } else {
          scope.row.targetBalance = delcommafy(el.target.value);
        }
      }
    },
    showBalanceWithUnit(currency) {
      return currency === 'USD' ? '($)' : `(${currency})`;
    },
    checkRules(row, type) {
      return {
        trigger: 'blur',
        validator: async (rule, value, callback) => {
          let triggerNotNull = row.triggerBalance != null && row.triggerBalance.length != 0;
          let targetNotNull = row.targetBalance != null && row.targetBalance.length != 0;
          let targetNull = row.targetBalance == null;

          if (triggerNotNull && targetNotNull) {
            let triggerValue = Number(delcommafy(row.triggerBalance));
            let targetValue = Number(delcommafy(row.targetBalance));

            switch (type) {
              case 'target':
                if (targetValue >= triggerValue) {
                  return callback(new Error(`Please enter an amount less than ${row.triggerBalance}`));
                }
                break;

              case 'trigger':
                if (triggerValue <= targetValue) {
                  return callback(new Error(`Please enter an amount greater than ${row.targetBalance}`));
                }
                break;
            }
          } else if (targetNull) {
            if (type == 'target') {
              return callback(new Error('Please enter content'));
            }
          } else if (targetNotNull && this.hasTriggerSwitch) {
            if (type == 'trigger') {
              return callback(new Error('Please enter content'));
            }
          }
          callback();
        },
      };
    },
    getContentTips(row, type) {
      let triggerNotNull = row.triggerBalance != null && row.triggerBalance.length != 0;
      let targetNotNull = row.targetBalance != null && row.targetBalance.length != 0;
      let targetNull = row.targetBalance == null;
      let triggerNull = row.triggerBalance == null;
      this.hasTriggerSwitch = row.triggerSwitch;
      let message = '';
      if (triggerNotNull && targetNotNull) {
        let triggerValue = Number(delcommafy(row.triggerBalance));
        let targetValue = Number(delcommafy(row.targetBalance));

        switch (type) {
          case 'target':
            if (targetValue < triggerValue) {
              row.targetDisabled = true;
              return;
            }
            message = `Please enter an amount less than ${triggerValue}`;
            row.targetDisabled = false;
            break;

          case 'trigger':
            if (triggerValue > targetValue) {
              row.triggerDisabled = true;
              return;
            }
            message = `Please enter an amount greater than ${targetValue}`;
            row.triggerDisabled = false;
            break;
        }
      } else if (targetNull && triggerNotNull) {
        message = 'Please enter content';
        row.targetDisabled = false;
        row.triggerDisabled = true;
      } else if (targetNotNull && triggerNull && this.hasTriggerSwitch) {
        message = 'Please enter content';
        row.targetDisabled = true;
        row.triggerDisabled = false;
      } else if (targetNotNull && !this.hasTriggerSwitch) {
        row.targetDisabled = true;
        row.triggerDisabled = true;
        return;
      } else {
        message = 'Please enter content';
        row.targetDisabled = false;
        row.triggerDisabled = false;
      }
      return message;
    },
    requiredStar(h, { column }) {
      return [h('span', { class: 'required-star', style: 'margin-left: 8px' }, column.label)];
    },
    addRequiredStar(h, { column }) {
      return [
        h('span', { class: { 'required-star': this.hasTriggerSwitch }, style: 'margin-left: 8px' }, column.label),
      ];
    },
    triggerHandleChange(row, rowIndex) {
      this.hasTriggerSwitch = row.triggerSwitch;
      // 
      if (!row.triggerSwitch) {
        let settlements = this.tableData;
        settlements[rowIndex].triggerBalance = null;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
/deep/ .columns {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  .el-form-item {
    margin-bottom: 0 !important;
    width: 100%;
  }
}
/deep/.required-star::before {
  left: 8px;
}
</style>
<style lang="scss">
.text-content-error-tooltip {
  padding: 16px;
  border-color: transparent !important;
  line-height: 14px !important;
  color: $--status_color--error !important;
  background-color: $--text_color--white !important;
  box-shadow: 0 0 10px rgba($color: $--text_color--emphasis, $alpha: 0.1);
  .popper__arrow {
    border-top-color: $--fill_color--dark !important;
  }
}
</style>
